@import "~bootstrap/dist/css/bootstrap.min.css";

@font-face {
  font-family: "Oswald-Regular";
  src: url(../fonts/Oswald/Oswald-Regular.ttf);
}
@font-face {
  font-family: "Oswald-Bold";
  src: url(../fonts/Oswald/Oswald-Bold.ttf);
}
@font-face {
  font-family: "Oswald-Medium";
  src: url(../fonts/Oswald/Oswald-Medium.ttf);
}
@font-face {
  font-family: "Oswald-Light";
  src: url(../fonts/Oswald/Oswald-Light.ttf);
}
@font-face {
  font-family: "Oswald-SemiBold";
  src: url(../fonts/Oswald/Oswald-SemiBold.ttf);
}
@font-face {
  font-family: "Oswald-ExtraLight";
  src: url(../fonts/Oswald/Oswald-ExtraLight.ttf);
}
@font-face {
  font-family: "Roboto-Regular";
  src: url(../fonts/Roboto/Roboto-Regular.ttf);
}
@font-face {
  font-family: "Roboto-Light";
  src: url(../fonts/Roboto/Roboto-Light.ttf);
}
@font-face {
  font-family: "Roboto-Bold";
  src: url(../fonts/Roboto/Roboto-Bold.ttf);
}
@font-face {
  font-family: "Roboto-Thin";
  src: url(../fonts/Roboto/Roboto-Thin.ttf);
}

#page-wraper::before {
  background: url(../images/pexels-nikolaos-dimou-2473183.jpg) no-repeat center center fixed;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  width: 100%;
  position: absolute;
  content: " ";
  display: block;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}

/* Basic */
body {
  font-family: "Roboto-Regular";
  font-size: 16px;
  overflow-x: hidden;
}

input,
select,
textarea {
  outline: none;
}

a,
a:hover {
  text-decoration: none;
}

p {
  font-size: 16px;
  line-height: 24px;
  color: #fff;
  font-family: "Oswald-Regular";
}

h4 {
  font-size: 26px;
  font-weight: 700;
  color: #fff;
  letter-spacing: 0.25px;
}

img {
  width: 100%;
  overflow: hidden;
}

.main-menu {
  padding-left: 0;
  padding: 0;
  margin: 0;
  list-style: none;
  li {
    display: inline-block;
  }
}

html {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  -webkit-box-sizing: inherit;
  -moz-box-sizing: inherit;
  box-sizing: inherit;
}

.clearfix {
  zoom: 1;
}

.clearfix:before,
.clearfix:after {
  content: "";
  display: table;
}

.clearfix:after {
  clear: both;
}

.container {
  margin: 0 auto;
}

.main-nav {
  text-align: center;
  li {
    display: block;
    a {
      height: 70px;
      line-height: 70px;
      display: inline-block;
      font-size: 16px;
      font-weight: 700;
      color: #fff;
      width: 100%;
      transition: all 0.5s;
      border-top: 2px solid #fff;
    }
    &:hover a,
    &.active a {
      background: #fff;
      color: #8c363e;
    }
  }

  li:last-child a {
    border-bottom: 2px solid #fff;
  }
}

/* Sidebar Menu */

.menu {
  left: 10%;
  -webkit-transition: -webkit-transform 233ms cubic-bezier(0, 0, 0.21, 1);
  -webkit-overflow-scrolling: touch;
  background-color: #a43f49;
  box-sizing: border-box;
  height: 100vh;
  max-height: 100vh !important;
  max-width: 80vw !important;
  min-width: 45px !important;
  outline: none;
  overflow-x: hidden !important;
  overflow-y: auto !important;
  padding: 0;
  position: fixed !important;
  top: calc(0px);
  width: 20%;
  will-change: transform;
  z-index: 9999 !important;
  text-align: center;
  .image {
    margin-top: 60px;
    img {
      width: 140px;
      height: 140px;
      border-radius: 50%;
      text-align: center;
    }
  }
  .author-content {
    margin: 40px 0px 60px 0px;
    h4 {
      margin-bottom: 0px;
    }
    span {
      font-size: 13px;
      font-style: italic;
      color: #fff;
    }
  }
  &:after,
  &:before {
    content: "";
    display: table;
  }
  &:after {
    clear: both;
  }
  a {
    text-decoration: none;
    color: inherit;
  }
  &::-webkit-scrollbar {
    display: none;
  }
}

.menu-toggle {
  color: #fff;
  cursor: pointer;
  font-size: 25px;
  position: fixed;
  right: 25px;
  top: 25px;
}

#menu-toggle {
  z-index: 999;
  cursor: pointer;
  position: fixed;
  top: 0;
  left: 0;
  color: #a43f49;
  background-color: #fff;
  width: 30px;
  height: 30px;
  text-align: center;
  display: inline-block;
  line-height: 60px;
}

.menu-close {
  cursor: pointer;
}

#menu-close {
  cursor: pointer;
  text-align: right;
  color: #a43f49;
  background-color: #fff;
  width: 100%;
  height: 30px;
  text-align: center;
  display: inline-block;
  line-height: 60px;
}

/* Section */

.section {
  color: #fff;
  width: 55%;
  right: 10%;
  left: 35%;
  position: relative;
  /* min-height: 100vh; */
  padding-bottom: 80px;
  border-bottom: 3px solid rgba(250, 250, 250, 0.25);
}

.section-heading {
  text-align: center;
  padding: 80px 45px;
  a {
    color: #fff;
    &:hover {
      color: #0a58ca;
      cursor: pointer;
    }
  }
  h2 {
    font-size: 32px;
    font-weight: 700;
    color: #fff;
  }
  .line-dec {
    width: 60px;
    height: 3px;
    background-color: #f1ce0a;
    margin: 10px auto;
  }
  span {
    margin-top: 5px;
    font-size: 16px;
    color: #fff;
    letter-spacing: 0.5px;
  }
}

.section-content {
  font-family: "Oswald-Regular";
  text-align: left;
  div:first-child {
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
    font-family: "Oswald-Bold";
  }
  div:nth-of-type(2) {
    text-align: left;
  }
  ul {
    text-align: left;
  }
  li::marker {
    font-size: 18px;
  }
}

section.about-me {
  span {
    font-family: "Oswald-Regular";
  }
  .contact-info {
    margin-top: 20px;
    font-family: "Roboto-Bold";
  }
  .row:first-child {
    margin-bottom: 20px;
  }
}

section.my-skills {
  .skill-badge {
    background-color: #f7f8f9;
    display: inline-block;
    padding: 5px 10px;
    margin: 3px 5px;
    color: #212529;
    font-family: "Oswald-Regular";
  }
}

section.my-certifications {
  ul {
    list-style: decimal;
    text-align: left;
    font-family: "Oswald-Regular";
    li {
      margin-bottom: 10px;
      svg {
        margin-left: 5px;
      }
    }
  }
}

section.my-work {
  .section-heading {
    text-align: center;
    padding: 80px 45px 50px 45px;
  }
  .section-divider {
    border-bottom: 2px solid #d9dfe4;
    margin: 10px auto;
    width: 80px;
  }
}

section.my-education {
  font-family: "Oswald-Regular";
  .edu-info {
    display: flex;
    justify-content: space-between;
    margin-bottom: 10;
  }
}

@media screen and (max-width: 1680px) {
  .menu {
    left: 5%;
  }

  .section {
    color: #fff;
    width: 65%;
    right: 5%;
    left: 30%;
  }
}

@media screen and (max-width: 1680px) and (min-width: 846px) {
  .menu .image {
    margin-top: 60px;
  }
}

@media screen and (max-width: 1280px) {
  .menu {
    left: 0%;
  }
}

.menu .container {
  padding-left: 0;
  padding-right: 0;
}

@media screen and (max-width: 1280px) and (min-width: 846px) {
  .menu {
    width: 25%;
  }
}

#menu-toggle,
#menu-close {
  display: none;
}

@media screen and (max-width: 845px) {
  #menu-toggle,
  #menu-close {
    display: inline-block;
  }

  .menu {
    width: 320px;
    -webkit-transform: translateX(-100%) !important;
    transform: translateX(-100%) !important;
    transition: -webkit-transform 233ms cubic-bezier(0, 0, 0.21, 1);
    transition: transform 233ms cubic-bezier(0, 0, 0.21, 1);
    transition: transform 233ms cubic-bezier(0, 0, 0.21, 1), -webkit-transform 233ms cubic-bezier(0, 0, 0.21, 1);
    .image {
      margin-top: 40px;
      img {
        width: 120px;
        height: 120px;
      }
    }
    .author-content {
      margin: 20px 0px 40px 0px;
    }
    .container {
      padding-left: 15px;
      padding-right: 15px;
    }
    &.open {
      display: inline-block;
      -webkit-transform: translateX(0) !important;
      transform: translateX(0) !important;
    }
  }

  .responsive-nav {
    display: inline-block;
  }
  .section {
    width: 94%;
    right: 3%;
    left: 3%;
  }
}
